const guid = '350d252399bc4a899f48fea09759fd2b';
const CryptoJS = require('crypto-js');

const CryptrService = {
  encryptString(plainText) {
    // prettier-ignore
    return CryptoJS.AES.encrypt(plainText, guid).toString();
  },

  decryptString(encryptedText) {
    // prettier-ignore
    const bytes = CryptoJS.AES.decrypt(encryptedText, guid);

    return bytes.toString(CryptoJS.enc.Utf8);
  },
};

export default CryptrService;
