import React, { useState, useEffect, useRef } from 'react';
import PropTypes from 'prop-types';
import { useNavigate } from 'react-router-dom';

import 'bootstrap/dist/css/bootstrap.min.css';
import Styles from './login.module.css';

import eyeIcon from '../../assets/icons/Eye.png';
import eyeOnIcon from '../../assets/icons/Eye-On.png';
import signInIcon from '../../assets/icons/Sign-In.svg';
import op2miseLogo from '../../assets/icons/op2mise.svg';

import FooterComponent from '../../shared/footer/footer.component';

import AuthService from '../../core/auth.service';
import CheckboxComponent from '../../shared/elements/checkbox/checkbox.component';
import CryptrService from '../../core/cryptr.service';

function LoginComponent({ callbackUserLogon }) {
  const navigate = useNavigate();
  const credentialListRef = useRef();
  const emailInputRef = useRef();

  const [loginParameters, setLoginParameters] = useState({
    email: '',
    password: '',
  });
  const [showPassword, setShowPassword] = useState(false);
  const [showButtonSpinner, setShowButtonSpinner] = useState(false);
  const [showErrorMessage, setShowErrorMessage] = useState(false);
  const [errorMessage, setErrorMessage] = useState(
    'Invalid email or password, please try again'
  );
  const [shouldRemember, setShouldRemember] = useState(
    AuthService.getRememberedCredentials().length > 0
  );
  const [showRememberedCredentials, setShowRememberedCredentials] = useState(
    false
  );
  const [rememberedCredentials] = useState(
    AuthService.getRememberedCredentials()
  );

  const togglePassword = () => {
    setShowPassword(!showPassword);
  };

  const onEnterTogglePassword = (e) => {
    if (e.key === 'Enter') togglePassword();
  };

  const onChangeSetLoginParameters = (e) => {
    setLoginParameters((prevState) => ({
      ...prevState,
      [e.target.name]: e.target.value,
      password: e.target.name === 'password' ? e.target.value : '',
      ssid:
        e.target.name === 'password'
          ? CryptrService.encryptString(e.target.value)
          : '',
    }));
  };

  const handleSubmit = () => {
    setShowButtonSpinner(true);
    setShowErrorMessage(false);
    AuthService.login(loginParameters)
      .then((response) => {
        AuthService.setUserIdentity(response.data);

        setTimeout(() => {
          callbackUserLogon(response.data.userId);
          if (response.data.roleId === 1) {
            navigate(`${process.env.REACT_APP_SUBFOLDER}/organisation`);
          } else {
            navigate(`${process.env.REACT_APP_SUBFOLDER}/channel-profiles`);
          }
          if (shouldRemember) {
            AuthService.rememberCredential(response.data);
          } else {
            AuthService.removeCredential(response.data);
          }
          setShowButtonSpinner(false);
        }, 500);
      })
      .catch((error) => {
        if (error.response.status === 401)
          setErrorMessage('Username or password incorrect.');
        else {
          setErrorMessage(error.response.data.message);
        }

        setShowErrorMessage(true);
        setShowButtonSpinner(false);
      })
      .finally(() => {});
  };

  const handleSubmitFromEnterKey = (e) => {
    if (e.key === 'Enter') {
      if (loginParameters.password !== '' && loginParameters.email !== '')
        handleSubmit();
    }

    if (e.which === 9) setShowRememberedCredentials(false);
  };

  const onCheckSetShouldRemember = (state) => {
    setShouldRemember(state);
  };

  const onFocusEmailInput = () => {
    setShowRememberedCredentials(true);
  };

  const onClickSetLoginParameters = (credential) => {
    setLoginParameters({
      email: credential.email,
      password: CryptrService.decryptString(credential.ssid),
    });
  };

  const redirectToForgotPasswordPage = () => {
    navigate(`${process.env.REACT_APP_SUBFOLDER}/forgot-password`);
  };

  const onEnterRedirectToForgotPasswordPage = (e) => {
    if (e.key === 'Enter') redirectToForgotPasswordPage();
  };

  const backdropClicked = (e) => {
    if (
      credentialListRef.current &&
      !credentialListRef.current.contains(e.target) &&
      emailInputRef.current &&
      !emailInputRef.current.contains(e.target)
    ) {
      setShowRememberedCredentials(false);
    }
  };

  useEffect(() => {
    if (rememberedCredentials.length > 0 && rememberedCredentials[0].ssid) {
      setLoginParameters({
        email: rememberedCredentials[0].email,
        password: CryptrService.decryptString(rememberedCredentials[0].ssid),
      });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    document.addEventListener('mousedown', backdropClicked);
    return () => document.removeEventListener('mousedown', backdropClicked);
  });

  return (
    <div className={Styles.body}>
      <div className={Styles.panel}>
        <div className="container">
          <div className="row py-3 text-center">
            <img
              className={`${Styles.virtualAiLogo} `}
              src={op2miseLogo}
              alt="op2miseLogo"
            />
            <div className={Styles.signInPanel}>
              <p className={Styles.title}>
                <div style={{ height: '30px' }} />
                Sign In
              </p>
              <input
                ref={emailInputRef}
                onFocus={onFocusEmailInput}
                className={Styles.emailInput}
                type="email"
                placeholder="Email"
                value={loginParameters.email}
                name="email"
                autoComplete="off"
                onChange={onChangeSetLoginParameters}
                onKeyDown={handleSubmitFromEnterKey}
              />
              {showRememberedCredentials && (
                <div ref={credentialListRef} className={Styles.credentialList}>
                  {loginParameters.email
                    ? AuthService.getRememberedCredentials()
                        .filter((o) => o.email.includes(loginParameters.email))
                        .map((credential) => (
                          <div
                            key={credential.id}
                            className={Styles.credentialListItem}
                            title={credential.email}
                            onClick={() =>
                              onClickSetLoginParameters(credential)
                            }
                            onKeyDown={() =>
                              onClickSetLoginParameters(credential)
                            }
                            role="presentation"
                          >
                            {credential.email}
                          </div>
                        ))
                    : AuthService.getRememberedCredentials().map(
                        (credential) => (
                          <div
                            key={credential.id}
                            className={Styles.credentialListItem}
                            title={credential.email}
                            onClick={() =>
                              onClickSetLoginParameters(credential)
                            }
                            onKeyDown={() =>
                              onClickSetLoginParameters(credential)
                            }
                            role="presentation"
                          >
                            {credential.email}
                          </div>
                        )
                      )}
                </div>
              )}
              <br />
              <div className="input-group position-relative">
                <input
                  className={Styles.passwordInput}
                  type={showPassword ? 'text' : 'password'}
                  placeholder="Password"
                  value={loginParameters.password}
                  name="password"
                  onChange={onChangeSetLoginParameters}
                  onKeyDown={handleSubmitFromEnterKey}
                />
                <div className={`${Styles.eyeIcon} input-group-append`}>
                  {showPassword ? (
                    <img
                      style={{
                        cursor: 'pointer',
                        height: '12.03px',
                        width: '15.03px',
                      }}
                      src={eyeOnIcon}
                      alt="EyeOnIcon"
                      onClick={togglePassword}
                      onKeyDown={onEnterTogglePassword}
                      role="presentation"
                      tabIndex={0}
                    />
                  ) : (
                    <img
                      style={{
                        cursor: 'pointer',
                      }}
                      src={eyeIcon}
                      alt="EyeIcon"
                      onClick={togglePassword}
                      onKeyDown={onEnterTogglePassword}
                      role="presentation"
                      tabIndex={0}
                    />
                  )}
                </div>
              </div>
              <div
                style={{
                  position: 'absolute',
                  marginLeft: '50px',
                  marginTop: '15px',
                }}
              >
                <CheckboxComponent
                  label="Remember me"
                  onCheck={onCheckSetShouldRemember}
                  disabled={showButtonSpinner}
                  active={shouldRemember}
                />
              </div>
              <p
                className={Styles.errorAlertText}
                style={{
                  display: showErrorMessage ? null : 'none',
                }}
              >
                {errorMessage}
              </p>
              <button
                className={`${Styles.signInButton} ${
                  showErrorMessage ? Styles.marginTop0 : ''
                }`}
                onClick={handleSubmit}
                disabled={
                  showButtonSpinner ||
                  loginParameters.password.length === 0 ||
                  loginParameters.email.length === 0
                }
                type="button"
              >
                Sign In{' '}
                {showButtonSpinner ? (
                  <i
                    className="fas fa-circle-notch fa-spin mr-1"
                    style={{
                      height: '20px',
                      width: '20px',
                    }}
                  />
                ) : (
                  <img
                    className={Styles.signInIcon}
                    src={signInIcon}
                    alt="SignInIcon"
                  />
                )}
              </button>
              <p
                className={Styles.redirectUrlText}
                style={{ cursor: 'pointer' }}
                onClick={redirectToForgotPasswordPage}
                onKeyDown={onEnterRedirectToForgotPasswordPage}
                role="presentation"
                tabIndex={0}
              >
                Forgot password?
              </p>
            </div>
          </div>
        </div>
      </div>
      <FooterComponent />
    </div>
  );
}
LoginComponent.propTypes = {
  callbackUserLogon: PropTypes.func.isRequired,
};
export default LoginComponent;
