import React, { useEffect, useState } from 'react';

import PropTypes from 'prop-types';

import styles from './styles.module.css';

function CheckboxComponent({
  label,
  shape,
  disabled,
  active,
  onCheck,
  checkBoxName,
  isLeft,
}) {
  const [isActive, setIsActive] = useState(active);

  // styles
  const form = shape === 'box' ? styles.box : shape === 'circle' && styles.circle;
  const isChecked = isActive ? styles.checked : styles.notChecked;
  const isDisabled = disabled && styles.disabled;
  // prettier-ignore
  const isDisabledChecked = disabled
        && (isActive ? styles.disabledChecked : styles.disabledNotChecked);

  useEffect(() => {
    setIsActive(active && active);
  }, [active]);

  const executeOnCheck = () => {
    setIsActive(active === true ? true : !isActive);
    onCheck(!isActive, checkBoxName);
  };

  const onKeyDownExecuteOnCheck = (e) => {
    if (e.key === 'Enter') executeOnCheck();
  };

  return (
    <div className={styles.checkboxDiv}>
      {isLeft && (
        <div
          className={`${form} ${isChecked} ${isDisabled} ${isDisabledChecked}`}
          onClick={executeOnCheck}
          onKeyDown={onKeyDownExecuteOnCheck}
          role="presentation"
          tabIndex={0}
        >
          <span className={styles.checkIcon} />
        </div>
      )}

      {label && (
        <div
          className={`${
            isLeft ? styles.label : styles.labelRight
          } ${!isActive && styles.labelDisabled}`}
        >
          {label}
        </div>
      )}
      {!isLeft && (
        <div
          className={`${form} ${isChecked} ${isDisabled} ${isDisabledChecked}`}
          onClick={executeOnCheck}
          onKeyDown={executeOnCheck}
          role="presentation"
        >
          <span className={styles.checkIcon} />
        </div>
      )}
    </div>
  );
}

CheckboxComponent.propTypes = {
  label: PropTypes.string,
  shape: PropTypes.string,
  disabled: PropTypes.bool,
  active: PropTypes.bool,
  onCheck: PropTypes.func,
  checkBoxName: PropTypes.string,
  isLeft: PropTypes.bool,
};

CheckboxComponent.defaultProps = {
  label: '',
  shape: 'box',
  disabled: false,
  active: true,
  onCheck: null,
  checkBoxName: '',
  isLeft: true,
};

export default CheckboxComponent;
