import React from 'react';
import { useLocation } from 'react-router-dom';
import styles from './footer.module.css';

function FooterComponent() {
  const location = useLocation();

  return (
    <div className={styles.vaiFooter}>
      <p
        className={`${
          location.pathname === `/${process.env.SUBFOLDER}/login`
            ? styles.vaiFooterTextGray
            : null
        } ${styles.vaiFooterText}`}
      >
        Virtual AI Ltd. Â© {new Date().getFullYear()}
      </p>
    </div>
  );
}

export default FooterComponent;
